function AIChatbot() {
  const [question, setQuestion] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [warning, setWarning] = useState('');

  const isHistoryQuestion = (text) => {
    const normalizedText = text.toLowerCase();
    return HISTORY_KEYWORDS.some(keyword => 
      normalizedText.includes(keyword.toLowerCase())
    );
  };

  const handleSubmit = async () => {
    if (!question.trim()) return;

    if (!isHistoryQuestion(question)) {
      setWarning('Vui lòng chỉ đặt câu hỏi liên quan đến lịch sử Việt Nam');
      return;
    }

    setWarning('');
    const userMessage = {
      type: 'user',
      content: question,
      timestamp: new Date().toISOString()
    };

    setChatHistory(prev => [...prev, userMessage]);
    setLoading(true);

    try {
      const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/chat`, {
        question: question,
      });

      if (response.data.error) {
        throw new Error(response.data.error);
      }

      const aiResponse = {
        type: 'ai',
        content: response.data.answer,
        images: response.data.images || [],
        relatedEvents: response.data.relatedEvents || [],
        relatedFigures: response.data.relatedFigures || [],
        timestamp: new Date().toISOString()
      };

      setChatHistory(prev => [...prev, aiResponse]);
    } catch (error) {
      console.error('Error:', error);
      const errorMessage = {
        type: 'ai',
        content: error.response?.data?.message || 
                 'Xin lỗi, đã có lỗi xảy ra trong quá trình xử lý. Vui lòng thử lại sau hoặc đặt câu hỏi khác.',
        timestamp: new Date().toISOString()
      };
      setChatHistory(prev => [...prev, errorMessage]);
    } finally {
      setLoading(false);
      setQuestion('');
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setQuestion(suggestion.question);
    setWarning('');
  };

  return (
    <ChatContainer>
      <Layout className="chat-layout">
        <Sider width={350} className="suggestions-sidebar">
          <Title level={4} style={{ margin: 0 }}>
            <HistoryOutlined /> Gợi ý câu hỏi
          </Title>
          
          {Object.entries(groupBy(HISTORY_SUGGESTIONS, 'category')).map(([category, suggestions]) => (
            <div key={category}>
              <h4 className="category-title">
                <Tag color="#1a237e">{category}</Tag>
              </h4>
              {suggestions.map(suggestion => (
                <div
                  key={suggestion.id}
                  className="suggestion-chip"
                  onClick={() => handleSuggestionClick(suggestion)}
                >
                  {suggestion.question}
                </div>
              ))}
            </div>
          ))}
        </Sider>

        <Content className="chat-content">
          <div className="chat-history">
            {chatHistory.map((message, index) => (
              <div key={index} className={`message ${message.type}`}>
                <div className="message-content">
                  <Paragraph>{message.content}</Paragraph>
                  
                  {message.type === 'ai' && message.images?.length > 0 && (
                    <div className="image-grid">
                      {message.images.map((img, i) => (
                        <div key={i} className="image-item">
                          <Image src={img.url} alt={img.caption} />
                          {img.caption && (
                            <div className="image-caption">{img.caption}</div>
                          )}
                        </div>
                      ))}
                    </div>
                  )}

                  {message.type === 'ai' && (message.relatedEvents?.length > 0 || message.relatedFigures?.length > 0) && (
                    <div className="related-content">
                      {message.relatedEvents?.length > 0 && (
                        <>
                          <div className="related-title">Sự kiện liên quan:</div>
                          <div className="related-list">
                            {message.relatedEvents.map((event, i) => (
                              <Tag key={i} color="blue">
                                <Link to={`/event/${event.id}`}>
                                  {event.title} ({event.date})
                                </Link>
                              </Tag>
                            ))}
                          </div>
                        </>
                      )}

                      {message.relatedFigures?.length > 0 && (
                        <>
                          <div className="related-title">Nhân vật liên quan:</div>
                          <div className="related-list">
                            {message.relatedFigures.map((figure, i) => (
                              <Tag key={i} color="green">
                                <Link to={`/figure/${figure.id}`}>
                                  {figure.name}
                                </Link>
                              </Tag>
                            ))}
                          </div>
                        </>
                      )}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>

          <div className="input-container">
            <div className="ant-input-textarea">
              <TextArea
                value={question}
                onChange={(e) => {
                  setQuestion(e.target.value);
                  setWarning('');
                }}
                placeholder="Hãy đặt câu hỏi về lịch sử Việt Nam..."
                autoSize={{ minRows: 1, maxRows: 3 }}
                onPressEnter={(e) => {
                  if (!e.shiftKey) {
                    e.preventDefault();
                    handleSubmit();
                  }
                }}
              />
              <Button 
                type="primary"
                icon={<SendOutlined />}
                onClick={handleSubmit}
                loading={loading}
              />
            </div>
            {warning && <div className="warning-message">{warning}</div>}
          </div>
        </Content>
      </Layout>
    </ChatContainer>
  );
}

// Helper function to group suggestions by category
function groupBy(array, key) {
  return array.reduce((result, item) => ({
    ...result,
    [item[key]]: [...(result[item[key]] || []), item],
  }), {});
}

export default AIChatbot; 