import { Typography, Card, Modal, Button, Collapse, Spin } from 'antd';
import './Generals.css';

const { Title, Paragraph } = Typography;

const generalsData = [
  {
    id: 1,
    name: "Hùng Vương",
    image: "/images/hung-vuong.jpg",
    wikiSearchTerms: ["Hùng Vương", "Vua Hùng", "Các vua Hùng"]
  },
  {
    id: 2,
    name: "Hai Bà Trưng",
    image: "/images/hai-ba-trung.jpg",
    wikiSearchTerms: ["Hai Bà Trưng", "Trưng Trắc", "Trưng Nhị"]
  },
  {
    id: 3,
    name: "Lý Nam Đế",
    image: "/images/ly-nam-de.jpg",
    wikiSearchTerms: ["Lý Nam Đế", "Lý Bí", "Lý Bôn"]
  },
  {
    id: 4,
    name: "Ngô Quyền",
    image: "/images/ngo-quyen.jpg",
    wikiSearchTerms: ["Ngô Quyền", "Tiền Ngô Vương"]
  },
  {
    id: 5,
    name: "Đinh Tiên Hoàng",
    image: "/images/dinh-tien-hoang.jpg",
    wikiSearchTerms: ["Đinh Tiên Hoàng", "Đinh Bộ Lĩnh"]
  },
  {
    id: 6,
    name: "Lê Đại Hành",
    image: "/images/le-dai-hanh.jpg",
    wikiSearchTerms: ["Lê Đại Hành", "Lê Hoàn"]
  },
  {
    id: 7,
    name: "Lý Thái Tổ",
    image: "/images/ly-thai-to.jpg",
    wikiSearchTerms: ["Lý Thái Tổ", "Lý Công Uẩn"]
  },
  {
    id: 8,
    name: "Lý Thường Kiệt",
    image: "/images/ly-thuong-kiet.jpg",
    wikiSearchTerms: ["Lý Thường Kiệt"]
  },
  {
    id: 9,
    name: "Trần Nhân Tông",
    image: "/images/tran-nhan-tong.jpg",
    wikiSearchTerms: ["Trần Nhân Tông", "Trần Khâm"]
  },
  {
    id: 10,
    name: "Trần Hưng Đạo",
    image: "/images/tran-hung-dao.jpg",
    wikiSearchTerms: ["Trần Hưng Đạo", "Trần Quốc Tuấn", "Hưng Đạo Vương"]
  },
  {
    id: 11,
    name: "Lê Thái Tổ",
    image: "/images/le-thai-to.jpg",
    wikiSearchTerms: ["Lê Thái Tổ", "Lê Lợi", "Bình Định Vương"]
  },
  {
    id: 12,
    name: "Nguyễn Trãi",
    image: "/images/nguyen-trai.jpg",
    wikiSearchTerms: ["Nguyễn Trãi", "Ức Trai"]
  },
  {
    id: 13,
    name: "Quang Trung",
    image: "/images/quang-trung.jpg",
    wikiSearchTerms: ["Quang Trung", "Nguyễn Huệ", "Bắc Bình Vương"]
  },
  {
    id: 14,
    name: "Hồ Chí Minh",
    image: "/images/ho-chi-minh.jpg",
    wikiSearchTerms: ["Hồ Chí Minh", "Nguyễn Sinh Cung", "Nguyễn Tất Thành", "Nguyễn Ái Quốc"]
  }
];

function Generals() {
  const [modalVisible, setModalVisible] = useState(false);
  const [selectedGeneral, setSelectedGeneral] = useState(null);
  const [loading, setLoading] = useState(false);
  const [generalDetail, setGeneralDetail] = useState(null);

  const summarizeWithGemini = useCallback(async (text) => {
    try {
      const response = await fetch('/api/wiki/summarize', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ text }),
      });

      if (!response.ok) {
        throw new Error('Failed to get summary');
      }

      const data = await response.json();
      return data.summary;
    } catch (error) {
      console.error('Error getting summary:', error);
      return text;
    }
  }, []);

  const extractBasicInfo = useCallback((wikiText) => {
    const info = {
      birthName: '',
      era: '',
      hometown: '',
      nation: '',
      capital: '',
      lifespan: ''
    };

    // Cải thiện patterns để bắt được nhiều trường hợp hơn
    const patterns = {
      birthName: /(?:tên\s*(?:húy|thật|đầy đủ|khai sinh))[^=]*=\s*([^}\n|]*)/i,
      era: /(?:triều\s*đại|thời\s*kỳ|hoàng\s*tộc)[^=]*=\s*([^}\n|]*)/i,
      hometown: /(?:quê\s*quán|nơi\s*sinh)[^=]*=\s*([^}\n|]*)/i,
      nation: /(?:quốc\s*hiệu)[^=]*(?:là|=)\s*([^}\n|]*)/i,
      capital: /(?:kinh\s*đô)[^=]*(?:là|=|tại)\s*([^}\n|]*)/i,
      lifespan: /(?:sinh\s*=\s*([^}\n|]*)).*?(?:mất\s*=\s*([^}\n|]*))|(\d{3,4})\s*[-–]\s*(\d{3,4})/i
    };

    // Hàm làm sạch text nâng cao
    const cleanText = (text) => {
      if (!text) return '';
      return text
        .replace(/\[\[[^\]]*\|([^\]]*)\]\]/g, '$1') // Wiki links
        .replace(/\[\[|\]\]/g, '') // Remaining brackets
        .replace(/\{\{[^}]*\}\}/g, '') // Templates
        .replace(/<[^>]*>/g, '') // HTML tags
        .replace(/\|.*$/, '') // Remove pipe and following content
        .replace(/\([^)]*\)/g, '') // Remove parentheses content
        .replace(/\s+/g, ' ') // Normalize spaces
        .replace(/^[=\s]+|[=\s]+$/g, '') // Trim equals signs and spaces
        .trim();
    };

    // Xử lý từng pattern
    Object.entries(patterns).forEach(([key, pattern]) => {
      const match = wikiText.match(pattern);
      if (match) {
        switch (key) {
          case 'lifespan':
            if (match[1] && match[2]) {
              const birthYear = cleanText(match[1]).match(/\d{3,4}/);
              const deathYear = cleanText(match[2]).match(/\d{3,4}/);
              if (birthYear && deathYear) {
                info[key] = `${birthYear[0]} - ${deathYear[0]}`;
              }
            } else if (match[3] && match[4]) {
              info[key] = `${match[3]} - ${match[4]}`;
            }
            break;
          case 'birthName':
            const nameMatch = match[1] || match[0];
            const cleanedName = cleanText(nameMatch)
              .split(/[,;]/)
              .map(n => cleanText(n))
              .filter(n => n.length > 1)
              .join(', ');
            info[key] = cleanedName;
            break;
          default:
            info[key] = cleanText(match[1] || match[0]);
        }
      }
    });

    // Xử lý trường hợp đặc biệt
    if (wikiText.includes('Đinh Tiên Hoàng')) {
      if (!info.nation) info.nation = 'Đại Cồ Việt';
      if (!info.capital) info.capital = 'Hoa Lư';
      if (!info.birthName) info.birthName = 'Đinh Bộ Lĩnh';
      if (!info.era) info.era = 'Nhà Đinh';
    }

    // Loại bỏ các giá trị không hợp lệ
    Object.keys(info).forEach(key => {
      if (info[key] && (
        info[key].includes('{{') ||
        info[key].includes('[[') ||
        info[key].includes('|') ||
        info[key].includes('=') ||
        info[key].length < 2
      )) {
        info[key] = '';
      }
    });

    return info;
  }, []);

  const extractAchievements = useCallback((content) => {
    const achievementKeywords = [
      'thành tựu', 'chiến thắng', 'chiến công', 'đóng góp',
      'công trạng', 'kỳ tích', 'di sản', 'tác phẩm',
      'giải thưởng', 'huân chương', 'danh hiệu'
    ];

    const achievements = [];
    const paragraphs = content.split('\n').filter(p => p.trim());

    paragraphs.forEach(paragraph => {
      if (achievementKeywords.some(keyword => paragraph.toLowerCase().includes(keyword))) {
        const sentences = paragraph.split(/[.!?]/).filter(s => s.trim());
        sentences.forEach(sentence => {
          if (achievementKeywords.some(keyword => sentence.toLowerCase().includes(keyword))) {
            achievements.push(sentence.trim());
          }
        });
      }
    });

    return achievements;
  }, []);

  const cleanWikiContent = useCallback((content) => {
    if (!content) return '';
    
    let cleanedContent = content
      // Chỉ loại bỏ các thẻ HTML không cần thiết
      .replace(/<script[^>]*>[\s\S]*?<\/script>/gi, '')
      .replace(/<style[^>]*>[\s\S]*?<\/style>/gi, '')
      .replace(/<!--[\s\S]*?-->/g, '')
      // Giữ lại các thẻ định dạng cơ bản
      .replace(/\[\[(?:[^|\]]*\|)?([^\]]+)\]\]/g, '$1') // Chỉ chuyển đổi wiki links
      .replace(/\{\{[^}]*\}\}/g, '') // Loại bỏ templates
      .replace(/http[s]?:\/\/[^\s]*/g, '') // Loại bỏ URLs
      .trim();

    return cleanedContent;
  }, []);

  const extractSectionContent = useCallback((fullText, sectionIndex) => {
    try {
      const tempDiv = document.createElement('div');
      tempDiv.innerHTML = fullText;

      // Tìm section theo index
      const section = tempDiv.querySelector(`[data-section="${sectionIndex}"]`);
      if (!section) {
        const sectionId = `mw-content-text-section-${sectionIndex}`;
        const altSection = tempDiv.querySelector(`#${sectionId}`);
        if (altSection) {
          // Giữ nguyên HTML formatting
          return altSection.innerHTML;
        }
        
        // Tìm theo heading
        const headings = tempDiv.querySelectorAll('h2, h3');
        for (const heading of headings) {
          if (heading.textContent.includes(sectionIndex)) {
            let content = '';
            let currentNode = heading.nextElementSibling;
            while (currentNode && !['H2', 'H3'].includes(currentNode.tagName)) {
              content += currentNode.outerHTML;
              currentNode = currentNode.nextElementSibling;
            }
            return content;
          }
        }
      }

      return section ? section.innerHTML : '';

    } catch (error) {
      console.error('Error extracting section content:', error);
      return '';
    }
  }, []);

  const processContentIntoSections = useCallback((content) => {
    if (!content) return [];

    // Tạo một div tạm để parse HTML content
    const tempDiv = document.createElement('div');
    tempDiv.innerHTML = content;

    // Xử lý các sections
    const sections = [];
    let currentSection = {
      title: 'Tổng quan',
      content: tempDiv.innerHTML
    };

    // Tìm tất cả các headings
    const headings = tempDiv.querySelectorAll('h2, h3, h4');
    headings.forEach((heading) => {
      if (currentSection.content) {
        sections.push({...currentSection});
      }
      
      // Lấy nội dung cho section mới
      let sectionContent = '';
      let currentNode = heading.nextElementSibling;
      while (currentNode && !['H2', 'H3', 'H4'].includes(currentNode.tagName)) {
        sectionContent += currentNode.outerHTML;
        currentNode = currentNode.nextElementSibling;
      }

      currentSection = {
        title: heading.textContent.trim(),
        content: sectionContent
      };
    });

    if (currentSection.content) {
      sections.push(currentSection);
    }

    return sections;
  }, []);

  const isImportantSection = useCallback((sectionTitle) => {
    const importantKeywords = [
      'tiểu sử', 'thân thế', 'cuộc đời', 'tuổi thơ',
      'sự nghiệp', 'hoạt động', 'chiến tích', 'chiến công',
      'thành tựu', 'cải cách', 'di sản', 'đóng góp',
      'ảnh hưởng', 'đánh giá', 'giai thoại', 'truyền thuyết',
      'gia đình', 'quê hương', 'xuất thân', 'học vấn',
      'tên gọi', 'danh hiệu', 'tước vị', 'chức vụ',
      'khởi nghĩa', 'chiến đấu', 'trị vì', 'cai trị',
      'bình luận', 'nhận xét', 'đánh giá'
    ];
    
    return importantKeywords.some(keyword => 
      sectionTitle.toLowerCase().includes(keyword)
    );
  }, []);

  const fetchWikiContent = useCallback(async (generalName, alternativeNames = []) => {
    setLoading(true);
    try {
      const baseUrl = `https://vi.wikipedia.org/w/api.php?origin=*&format=json`;
      
      // 1. Tìm trang Wikipedia phù hợp với các từ khóa tìm kiếm
      const searchPromises = [generalName, ...alternativeNames].map(term => 
        fetch(`${baseUrl}&action=query&list=search&prop=extracts|info&inprop=url&explaintext=1&exintro=1&srsearch=${encodeURIComponent(term)}&srnamespace=0&srlimit=1`)
          .then(r => r.json())
      );

      const searchResults = await Promise.all(searchPromises);
      const bestMatch = searchResults.find(result => result.query?.search?.length > 0);

      if (!bestMatch?.query?.search?.length) {
        throw new Error('Không tìm thấy thông tin');
      }

      const pageTitle = bestMatch.query.search[0].title;
      
      // 2. Lấy thông tin chi tiết và hình ảnh
      const [basicInfo, contentData, imageInfo] = await Promise.all([
        fetch(`${baseUrl}&action=query&prop=extracts|info|revisions&inprop=url&rvprop=content&titles=${encodeURIComponent(pageTitle)}&format=json`).then(r => r.json()),
        fetch(`${baseUrl}&action=parse&page=${encodeURIComponent(pageTitle)}&prop=sections|text|images&contentmodel=wikitext`).then(r => r.json()),
        fetch(`${baseUrl}&action=query&titles=${encodeURIComponent(pageTitle)}&prop=pageimages|images&piprop=original&iiprop=url&format=json`).then(r => r.json())
      ]);

      const pageId = Object.keys(basicInfo.query.pages)[0];
      const page = basicInfo.query.pages[pageId];
      
      // 3. Xử lý hình ảnh
      let mainImage = selectedGeneral.image; // Mặc định dùng ảnh từ data
      if (imageInfo.query?.pages[pageId]?.original?.source) {
        mainImage = imageInfo.query.pages[pageId].original.source;
      } else if (imageInfo.query?.pages[pageId]?.images) {
        const relevantImages = imageInfo.query.pages[pageId].images
          .filter(img => {
            const title = img.title.toLowerCase();
            return (
              title.includes(generalName.toLowerCase()) &&
              !title.includes('icon') &&
              !title.includes('logo') &&
              (title.includes('.jpg') || title.includes('.png'))
            );
          });
        if (relevantImages.length > 0) {
          const imageUrl = await fetch(`${baseUrl}&action=query&titles=${encodeURIComponent(relevantImages[0].title)}&prop=imageinfo&iiprop=url`)
            .then(r => r.json())
            .then(data => {
              const imgPageId = Object.keys(data.query.pages)[0];
              return data.query.pages[imgPageId]?.imageinfo?.[0]?.url;
            });
          if (imageUrl) mainImage = imageUrl;
        }
      }

      // 4. Xử lý nội dung và thông tin cơ bản
      const wikiText = page.revisions[0]['*'];
      const basicDetails = extractBasicInfo(wikiText);
      
      // Đảm bảo thông tin cơ bản không bị trống
      if (!basicDetails.birthName && generalName !== pageTitle) {
        basicDetails.birthName = pageTitle;
      }

      // Xử lý các sections
      const sections = contentData.parse?.sections || [];
      const fullText = contentData.parse?.text['*'] || '';
      
      const processedSections = sections
        .filter(section => isImportantSection(section.line))
        .map(section => ({
          title: section.line,
          content: processContentIntoSections(extractSectionContent(fullText, section.index))
        }));

      // 5. Tạo tóm tắt và trích xuất thành tựu
      const cleanedExtract = cleanWikiContent(page.extract);
      const [summary, achievements] = await Promise.all([
        summarizeWithGemini(cleanedExtract),
        extractAchievements(fullText)
      ]);

      // 6. Cập nhật state
      setGeneralDetail({
        name: pageTitle,
        image: mainImage,
        summary,
        ...basicDetails,
        sections: processedSections.map(section => ({
          ...section,
          content: section.content.map(subsection => ({
            ...subsection,
            content: subsection.content.map(cleanWikiContent)
          }))
        })),
        achievements,
        url: page.fullurl
      });

    } catch (error) {
      console.error('Error:', error);
      if (selectedGeneral) {
        setGeneralDetail({
          ...selectedGeneral,
          summary: 'Không thể tải thông tin từ Wikipedia.',
          sections: [],
          achievements: []
        });
      }
    } finally {
      setLoading(false);
    }
  }, [
    extractBasicInfo, 
    processContentIntoSections, 
    extractSectionContent, 
    extractAchievements, 
    isImportantSection, 
    summarizeWithGemini, 
    cleanWikiContent,
    selectedGeneral
  ]);

  const handleGeneralClick = (general) => {
    setSelectedGeneral(general);
    setModalVisible(true);
    setGeneralDetail(null);
    setLoading(true);
    fetchWikiContent(general.name, general.wikiSearchTerms);
  };

  return (
    <div className="generals-page">
      <Title level={2} className="page-title">
        Các Tướng Lĩnh Trong Lịch Sử Việt Nam
      </Title>

      <div className="generals-grid">
        {generalsData.map(general => (
          <Card
            key={general.id}
            hoverable
            className="general-card"
            cover={<img alt={general.name} src={general.image} />}
            onClick={() => handleGeneralClick(general)}
          >
            <Card.Meta
              title={general.name}
            />
          </Card>
        ))}
      </div>

      <Modal
        title={null}
        open={modalVisible}
        onCancel={() => {
          setModalVisible(false);
          setGeneralDetail(null);
          setLoading(false);
        }}
        width={1200}
        footer={null}
        className="general-modal"
      >
        <div className="general-detail">
          <Spin 
            spinning={loading}
            tip="Đang tải thông tin..."
            size="large"
          >
            {generalDetail ? (
              <div className="content-layout">
                <div className="header-section">
                  <div className="general-image">
                    <img src={generalDetail.image} alt={generalDetail.name} />
                  </div>
                  <div className="general-info">
                    <Title level={2}>{generalDetail.name}</Title>
                    <div className="info-grid">
                      <Paragraph>
                        <strong>Tên thật:</strong> {generalDetail.birthName}
                      </Paragraph>
                      <Paragraph>
                        <strong>Thời kỳ:</strong> {generalDetail.era}
                      </Paragraph>
                      <Paragraph>
                        <strong>Năm sinh - mất:</strong> {generalDetail.lifespan}
                      </Paragraph>
                      <Paragraph>
                        <strong>Quê quán:</strong> {generalDetail.hometown}
                      </Paragraph>
                      <Paragraph>
                        <strong>Quốc hiệu:</strong> {generalDetail.nation}
                      </Paragraph>
                      <Paragraph>
                        <strong>Kinh đô:</strong> {generalDetail.capital}
                      </Paragraph>
                    </div>
                  </div>
                </div>

                <div className="main-content">
                  <Collapse defaultActiveKey={['1']} className="content-collapse">
                    <Collapse.Panel header="Tóm tắt chi tiết" key="1">
                      <div className="summary-content">
                        <div dangerouslySetInnerHTML={{ __html: generalDetail.summary }} />
                      </div>
                    </Collapse.Panel>

                    {generalDetail.sections.map((section, index) => (
                      <Collapse.Panel 
                        header={section.title} 
                        key={index + 3}
                        className="section-panel"
                      >
                        <div 
                          className="content-section"
                          dangerouslySetInnerHTML={{ __html: section.content }}
                        />
                      </Collapse.Panel>
                    ))}

                    <Collapse.Panel header="Thành tựu nổi bật" key="last">
                      <div className="achievements-section">
                        <div className="achievements-source">
                          <Title level={5}>Từ tư liệu lịch sử:</Title>
                          <ul className="achievements-list">
                            {generalDetail.achievements.map((achievement, index) => (
                              <li key={`original-${index}`} className="achievement-item source-original">
                                {achievement}
                              </li>
                            ))}
                          </ul>
                        </div>

                        {generalDetail.achievements.length > 0 && (
                          <div className="achievements-source">
                            <Title level={5}>Từ Wikipedia:</Title>
                            <ul className="achievements-list">
                              {generalDetail.achievements
                                .filter(achievement => !generalDetail.achievements.includes(achievement))
                                .map((achievement, index) => (
                                  <li key={`wiki-${index}`} className="achievement-item source-wiki">
                                    {achievement}
                                    <span className="source-tag">Wikipedia</span>
                                  </li>
                                ))}
                            </ul>
                          </div>
                        )}
                      </div>
                    </Collapse.Panel>
                  </Collapse>

                  <div className="wiki-source">
                    <a href={generalDetail.url} target="_blank" rel="noopener noreferrer">
                      Xem bài viết đầy đủ trên Wikipedia
                    </a>
                  </div>
                </div>
              </div>
            ) : (
              <div className="loading-placeholder">
                <Title level={3}>{selectedGeneral?.name}</Title>
                <Paragraph>Đang tải thông tin chi tiết...</Paragraph>
              </div>
            )}
          </Spin>
        </div>
      </Modal>
    </div>
  );
}

export default Generals;